import React from "react";
import { Helmet } from "react-helmet";
import LayoutPage from "../Layout";
import axios from "axios";
import { toast } from "react-toastify";
import { useFormik } from "formik";
import { useState } from "react";
import { useNavigate } from "react-router-dom";

function AddNew() {
  const [disabledSubmit, setDisabledSubmit] = useState(false);
  const [initialValues, setInitialValues] = useState({
    mediaFile: "",
    championship: "",
    sportsCategory: "",
  });
  // const [initialValues, setInitialValues] = useState({ nationality_name: ''})
  const navigate = useNavigate();

  // const handleFileChange = event => {
  //     console.log(event.target.files[0]);

  // }
  const validate = (values) => {
    const errors = {};

    if (!values.mediaFile) {
      errors.mediaFile = " mediaFile is required";
    } else if (values.championship.length < 3) {
      errors.championship = " championship min length is 3 characters";
    } else if (values.sportsCategor.length > 50) {
      errors.sportsCategory = "sportsCategor max length is 50 characters";
    }
    return errors;
  };
  const formik = useFormik({
    initialValues: initialValues,
    validate,
    onSubmit: async (values) => {
      console.log(values);
      // var formData = new FormData()
      // formData.append("nationality_name",values?.nationality_name)
      // formData.append("category_desc", values.category_desc)
      // formData.append("categoryfile", values.categoryfile)
      setDisabledSubmit(true);
      try {
        // console.log(formData);
        let res = await axios.post(`admin/countryadd`, values);
        // console.log(res);
        navigate("/country", { state: { message: res.data.message } });
      } catch (errors) {
        if (errors.response.data.error) {
          toast(errors.response.data.error.message, {
            position: "top-right",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            type: "error",
          });
        } else {
          toast(errors.response.data.message, {
            position: "top-right",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            type: "error",
          });
        }

        setDisabledSubmit(false);
      }
    },
  });



  
  return (
    <>
      <Helmet>
        <title>Add Game</title>
      </Helmet>
      <LayoutPage>
        <div className="row">
          <div className="col-12">
            <div className="card my-4">
              <div className="card-header p-0 position-relative mt-n4 mx-3 z-index-2">
                <div className="bg-gradient-primary shadow-primary border-radius-lg pt-4 pb-3">
                  <h6 className="text-white text-capitalize ps-3">
                    Add New Game
                  </h6>
                </div>
              </div>
              <div className="card-body px-0 pb-2">
                <div className="p-4">
                  <form onSubmit={formik.handleSubmit}>
                    <div className="input-group input-group-outline mb-3">
                      <label>media File</label>
                      <input
                        style={{ width: "100%" }}
                        type="file"
                        id="mediaFile"
                        name="mediaFile"
                        className="form-control"
                        placeholder="mediaFile"
                        onChange={(event) => {
                          formik.setFieldValue(
                            "mediaFile",
                            event.target.files[0]
                          );
                        }}
                      />
                    </div>
                    {formik.errors.mediaFile ? (
                      <div className="text-danger">
                        {formik.errors.mediaFile}
                      </div>
                    ) : null}

                    <div className="input-group input-group-outline mb-3">
                      <label>Championship</label>

                      <input
                        style={{ width: "100%" }}
                        type="text"
                        id="championship"
                        name="championship"
                        className="form-control"
                        autoComplete="off"
                        placeholder="championship"
                        value={formik.values.championship || ""}
                        onChange={formik.handleChange}
                      />
                    </div>
                    {formik.errors.championship ? (
                      <div className="text-danger">
                        {formik.errors.championship}
                      </div>
                    ) : null}

                    <div className="input-group input-group-outline mb-3">
                      <label>Sports Category</label>
                      <input
                        style={{ width: "100%" }}
                        type="text"
                        id="sportsCategory"
                        name="sportsCategory"
                        className="form-control"
                        autoComplete="off"
                        placeholder="sportsCategory"
                        value={formik.values.sportsCategory || ""}
                        onChange={formik.handleChange}
                      />
                    </div>
                    {formik.errors.sportsCategory ? (
                      <div className="text-danger">
                        {formik.errors.sportsCategory}
                      </div>
                    ) : null}

                    <div className="text-center">
                      <button
                        type="button"
                        onClick={() => navigate("/game/addgame")}
                        className="btn btn-lg bg-gradient-primary btn-lg w-20 mt-4 mb-0"
                        disabled={disabledSubmit}
                      >
                        Cancel
                      </button>
                      &nbsp;&nbsp;
                      <button
                        type="submit"
                        className="btn btn-lg bg-gradient-primary btn-lg w-20 mt-4 mb-0"
                        disabled={disabledSubmit}
                      >
                        {disabledSubmit ? (
                          <div>
                            <span
                              className="spinner-border spinner-border-sm"
                              role="status"
                              aria-hidden="true"
                            ></span>
                            <span className="sr-only"></span> Submitting
                          </div>
                        ) : (
                          "Submit"
                        )}
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </LayoutPage>
    </>
  );
}

export default AddNew;
